import React, { useState, useRef, useEffect } from 'react';
import { Link, RouteComponentProps } from 'react-router-dom';
import { useQuery } from '@apollo/react-hooks';
import { Affix, List, Layout, Typography } from 'antd';
import { ErrorBanner, ListingCard } from '../../lib/components/';
import { LISTINGS } from '../../lib/graphql/queries/Listings';
import { 
  Listings as ListingsData, ListingsVariables 
} from '../../lib/graphql/queries/Listings/__generated__/Listings';
import { ListingsFilter } from '../../lib/graphql/globalTypes';
import { ListingsFilters, ListingsPagination, ListingsSkeleton } from './components/';
interface MatchParams {
  location: string;
}

const { Content } = Layout;
const { Title, Paragraph, Text } = Typography;

const PAGE_LIMIT = 8;

export const Listings = ({ match }: RouteComponentProps<MatchParams>) => {
  const locationRef = useRef(match.params.location);
  const [filter, setFilter] = useState(ListingsFilter.PRICE_LOW_TO_HIGH); 
  const [page, setPage] = useState(1);

  const { data, loading, error } = useQuery<ListingsData, ListingsVariables>(LISTINGS, {
    skip: locationRef.current !== match.params.location && page !== 1,
    variables: {
      location: match.params.location,
      filter: filter,
      limit: PAGE_LIMIT,
      page
    }
  });

  useEffect(() => {
    setPage(1);
    locationRef.current = match.params.location;
  }, [match.params.location]);

  const listings = data ? data.listings : null;
  const listingsRegion = listings ? listings.region : null;

  const listingsRegionElement = listingsRegion ? (
    <Title level={3} className="listings__title">Results for "{listingsRegion}"</Title>
  ) : null;

  const listingsSectionElement = 
    listings && listings.result.length ? (
    <div>
      <div className="listings__affix">
      <Affix offset={64}>
        <ListingsPagination 
          total={listings.total} 
          page={page} 
          limit={PAGE_LIMIT} 
          setPage={setPage}
        />
      </Affix>
      <Affix offset={64}>
        <ListingsFilters filter={filter} setFilter={setFilter} />
      </Affix>
      </div>
      <List 
        grid={{
          gutter: 8,
          xs: 1,
          sm: 2,
          lg: 4,
        }}
        dataSource={listings.result}
        renderItem={listing => (
          <List.Item>
            <ListingCard listing={listing}/>
          </List.Item>
        )}
      /> 
    </div>
  ) : (
    <div>
      <Paragraph> It appears there are no listings yet created for{" "} 
        <Text mark>{listingsRegion}</Text>
      </Paragraph>
      <Paragraph> 
        Be the first person to create a <Link to="/host">listing in this area</Link>.
      </Paragraph>
    </div>
  );

  if (loading) {
    return (
      <Content>
         <ListingsSkeleton />;
      </Content>
    )
  }

  if (error) {
    return (
      <Content>
        <ErrorBanner description="Either your search had zero results or we've encountered an error. Please check your search has common keywords."/>
        <ListingsSkeleton />;
      </Content>
    )
  }

  return (
    <Content className="listings">
      {listingsRegionElement}
      {listingsSectionElement}
    </Content>
  )
}