import React from 'react';
import { Modal, Divider, Button, Icon, Typography } from 'antd';
import moment, { Moment } from 'moment';
import { CardElement, injectStripe, ReactStripeElements } from 'react-stripe-elements';
import { useMutation } from '@apollo/react-hooks';
import { CREATE_BOOKING } from '../../../../lib/graphql/mutations/';
import { 
  CreateBooking as CreateBookingData, CreateBookingVariables 
} from '../../../../lib/graphql/mutations/CreateBooking/__generated__/CreateBooking';
import { displayErrorMessage, displayPrice, displaySuccessNotification } from '../../../../lib/utils';

const { Text, Title, Paragraph } = Typography;

interface Props {
  id: string;
  price: number;
  checkInDate: Moment;
  checkOutDate: Moment;
  modalVisible: boolean;
  setModalVisible: (modalVisible: boolean) => void;
  clearBookingData: () => void;
  handleListingRefetch: () => Promise<void>;
}

export const ListingCreateBookingModal = ({ 
  id,
  price, 
  checkInDate, 
  checkOutDate, 
  modalVisible, 
  setModalVisible,
  clearBookingData,
  handleListingRefetch,
  stripe
}: Props & ReactStripeElements.InjectedStripeProps) => {
  const [ createBooking, { loading }] = useMutation<CreateBookingData, CreateBookingVariables>(CREATE_BOOKING, {
    onCompleted: () => {
      clearBookingData();
      displaySuccessNotification("You've successfully created this booking! You can find booking information on your profile page.")
      handleListingRefetch();
    },
    onError: () => {
      displayErrorMessage("Failed to create this booking, please try again.")
    }
  });
  const daysBooked = checkOutDate.diff(checkInDate, "days") + 1;
  const listingPrice = daysBooked * price;

  const handleCreateBooking = async () => {
    if (!stripe) {
      return displayErrorMessage("Sorry, we could not connect to stripe. Please try again.");
    }

    let { token: stripeToken, error } = await stripe.createToken();

    if (stripeToken) {
      createBooking({
        variables: {
          input: {
            id,
            source: stripeToken.id,
            checkIn: moment(checkInDate).format("YYYY-MM-DD"),
            checkOut: moment(checkOutDate).format("YYYY-MM-DD")
          }
        }
      });
    } else {
      displayErrorMessage(error && error.message 
        ? error.message 
        : "Sorry, we weren't able to book this listing. Please try again."
      );
    } 
  };
  
  return (
    <Modal 
      visible={modalVisible}
      centered
      onCancel={() => setModalVisible(false)}
      footer={null}
    >
      <div className="listing-booking-modal">
        <div className="listing-booking-modal__intro">  
          <Title className="listing-booking-modal__intro-title">
            <Icon type="key" />
          </Title>
          <Title level={3} className="listing-booking-modal__intro-title">
            Book Your Trip
          </Title>
          <Paragraph>
          Enter your payment information to book the listing from the dates between{" "} 
            <Text mark strong>{moment(checkInDate).format("MMMM Do YYYY")}</Text>
            and {" "}
            <Text mark strong>{moment(checkOutDate).format("MMMM Do YYYY")}</Text>{" "}
            , inclusive.
          </Paragraph>
        </div>
        <Divider />
        <div className="listing-booking-modal__charge-summary">
          <Paragraph>
            {price} x {daysBooked} days ={" "} 
            <Text strong>{listingPrice}</Text>
          </Paragraph>
          <Paragraph className="listing-booking-modal__charge-summary-total">
            Total <Text strong>{listingPrice}</Text>
          </Paragraph>
        </div>

        <Divider />
        <div className="listing-booking-modal__stripe-card-section">
          <CardElement hidePostalCode className="listing-booking-modal__stripe-card" />
          <Button 
            type="primary" 
            size="large" 
            className="listing-booking-modal__cta"
            loading={loading}
            onClick={handleCreateBooking}
          >
            Book
          </Button>
        </div>
      </div>
    </Modal>
  )
}

export const WrappedListingCreateBookingModal = injectStripe(ListingCreateBookingModal);