import React, { useEffect, useRef } from 'react';
import { Redirect } from 'react-router-dom';
import { Card, Layout, Spin, Typography } from 'antd';
import { Viewer } from '../../lib/types';
import { ErrorBanner } from '../../lib/components';
import { AUTH_URL } from '../../lib/graphql/queries';
import { LOG_IN } from '../../lib/graphql/mutations';
import { LogIn as LogInData, LogInVariables } from '../../lib/graphql/mutations/LogIn/__generated__/LogIn';
import { AuthUrl as AuthUrlData } from '../../lib/graphql/queries/AuthUrl/__generated__/AuthUrl';
import { useApolloClient, useMutation } from '@apollo/react-hooks';
import { displaySuccessNotification, displayErrorMessage } from '../../lib/utils';

import googleLogo from './assets/google_logo.jpg';

interface Props {
  setViewer: (viewer: Viewer) => void;
}

const { Content } = Layout;
const { Text, Title } = Typography;

export const Login = ({ setViewer }: Props) => {
  const client = useApolloClient();

  const [logIn, { 
    data: LogInData, error: logInError, loading: logInLoading 
  }] = useMutation<LogInData, LogInVariables>(LOG_IN, {
    onCompleted: data => {
      if (data && data.logIn && data.logIn.token) {
        setViewer(data.logIn);
        sessionStorage.setItem('token', data.logIn.token);
        displaySuccessNotification('You successfully logged in!');
      }
    } 
  });
  
  const LogInRef = useRef(logIn);

  useEffect(() => {
    const code = new URL(window.location.href).searchParams.get("code");

    if (code) {
      LogInRef.current({
        variables: {
          input: { code }
        }
      });
    }
  }, []);

  const handleAuthorize = async () => {
    try {
      const { data } = await client.query<AuthUrlData>({
        query: AUTH_URL
      });
      window.location.href = data.authUrl;
    } catch { 
      displayErrorMessage("Sorry, we weren't able to log you in.")
    }
  }

  if (logInLoading) {
    return (
      <Content>
        <Spin size="large" tip="logging you in..." />
      </Content>
    )
  }

  if (LogInData && LogInData.logIn) {
    const { id: viewerId } = LogInData.logIn;
    return (
      <Redirect to={`/user/${viewerId}`} />
    );
  }

  const logInErrorBannerElement = logInError ? (
    <ErrorBanner description="Sorry, we weren't able to log you in."/>
  ): null;
  
  return (
    <Content className="log-in">
      {logInErrorBannerElement}
      <Card className="log-in-card">
        <div className="log-in-card__intro">
          <Title level={3} className="log-in-card__intro-title">
            <span role="img" aria-label="wave">
              🏠
            </span>
          </Title>
          <Title level={3} className="log-in-card__intro-title">
            Log in to TinyHouse!
          </Title>
          <Text>Sign in with Google to start booking available rentals!</Text>
          <button className="log-in-card__google-button" onClick={handleAuthorize}>
            <img src={googleLogo} alt="google logo" className="log-in-card__google-button-logo" />
            <span className="log-in-card__google-button-text">
            Log in with Google
          </span>
          </button>
          
          <Text type="secondary">note: signing in with Google you will be redirected to a Google consent form to sign into your account!</Text>
        </div>
      </Card>
    </Content>
  )
};