import React, { useState, FormEvent } from 'react';
import { useMutation } from '@apollo/react-hooks';
import { FormComponentProps } from 'antd/lib/form';
import { Link, Redirect } from 'react-router-dom';
import { Button, Layout, Input, Icon, Upload, Radio, InputNumber, Typography, Form } from 'antd';
import { UploadChangeParam } from 'antd/lib/upload';
import { Viewer } from '../../lib/types';
import { ListingType } from '../../lib/graphql/globalTypes';
import { displayErrorMessage, displaySuccessNotification } from '../../lib/utils';
import { HOST_LISTING } from '../../lib/graphql/mutations/HostListing';
import { hostListing as hostListingData, hostListingVariables } from '../../lib/graphql/mutations/HostListing/__generated__/hostListing';

interface Props {
  viewer: Viewer;
}

const { Content } = Layout;
const { Text, Title } = Typography;
const { Item } = Form;

const beforeImageUpload = ( file: File ) => {
  const fileValidImage = file.type === "image/jpeg" || file.type === "image/png";
  const fileIsValidSize = file.size / 1024 / 1024 < 1;

  if (!fileValidImage) {
    displayErrorMessage("Image must be of type JPG or PNG");
    return false;
  }

  if (!fileIsValidSize) {
    displayErrorMessage("Image must be less than 1 MB");
    return false;
  }

  return fileIsValidSize && fileValidImage;
}

export const Host = ({ viewer, form }: Props & FormComponentProps) => {
  const [hostListing, { loading, data }] = useMutation<hostListingData, hostListingVariables>(HOST_LISTING, {
    onCompleted: () => {
      displaySuccessNotification("You've successfully created your listing!");
    },
    onError: () => {
      displayErrorMessage("Sorry, we weren't able to create your listing. Please try again.");
    }
  });

  const [imageLoading, setImageLoading] = useState(false);
  const [imageBase64Value, setImageBase64Value] = useState<string | null>(null);
  
  const handleImageUpload = (info: UploadChangeParam) => {
    const { file } = info;

    if (file.status === "uploading") {
      setImageLoading(true);
      return;
    }

    if (file.status === "done" && file.originFileObj) {
      getBase64Value(file.originFileObj, imageBase64Value => {
          setImageBase64Value(imageBase64Value);
          setImageLoading(false);
        }
      )
    }
  }

  const getBase64Value = (
    img: File | Blob, 
    callback: (imageBase64Value: string) => void
  ) => {
    const reader = new FileReader();
    reader.readAsDataURL(img);
    reader.onload = () => {
      callback(reader.result as string);
    }
  }

  const { getFieldDecorator } = form;

  const handleHostListing = ( evt: FormEvent) => {
    evt.preventDefault();

    form.validateFields((err, values) => {
      if (err) {
        displayErrorMessage("Please complete all fields.");
        return;
      }

      const fullAddress = `${values.address}, ${values.city}, ${values.state}, ${values.postalCode}`;

      const input = {
        ...values,
        address: fullAddress,
        image: imageBase64Value,
        price: values.price * 100
      };
      delete input.city;
      delete input.state;
      delete input.postalCode;

      hostListing({
        variables: {
          input
        }
      });
    });
  };

  if (!viewer.id || !viewer.hasWallet) {
    return (
      <Content className="host-content">
        <div className="host__form-header">
          <Title level={3} className="host__form-title">You must be signed in and connected to Stripe to host a listing!</Title>
          <Text type="secondary">We only allow users that are signed in and have connected to Stripe to host new listings.
          You can sign in or sign up on the <Link to="/login">Login</Link> page and connect with Stripe.</Text>
        </div>
      </Content>
    ) 
  }

  if (loading) {
    return (
      <Content className="host-content">
        <div className="host__form-header">
          <Title level={3} className="host__form-title">Please wait!</Title>
            <Text type="secondary">We're creating your listing now.</Text>
        </div>
      </Content>
    )
  }

  if (data && data.hostListing) {
    return <Redirect to={`/listing/${data.hostListing.id}`} />;
  }
  return (
    <Content className="host-content">
      <Form layout="vertical" onSubmit={handleHostListing}>
        <div className="host__form-header">
          <Title className="host__form-title">Hi! Let's get started listing your place on TinyHouse!</Title>
          <Text type="secondary">In this form we'll collect basic information about your listing.</Text>
        </div>
        <Item label="Home Type">
        {
          getFieldDecorator("type", {
            rules: [
              {
                required: true,
                message: "Please select a home type"
              }
            ]
          })(
            <Radio.Group>
            <Radio.Button value={ListingType.APARTMENT}>
              <Icon type="bank" /><span>Apartment</span>
            </Radio.Button>
            <Radio.Button value={ListingType.HOUSE}>
              <Icon type="home" /><span>House</span>
            </Radio.Button>
          </Radio.Group>
        )}
        </Item>

        <Item label="Number of Guests">
        {
          getFieldDecorator("numOfGuests", {
            rules: [
              {
                required: true,
                message: "Please select max number of guests."
              }
            ]
          })(
          <InputNumber min={1} placeholder="4" />
        )}
        </Item>

        <Item label="Title" extra="Max length of 45 characters">
        {
          getFieldDecorator("title", {
            rules: [
              {
                required: true,
                message: "Please give your listing a title."
              }
            ]
          })(
          <Input maxLength={45} placeholder="The iconic and luxurious Bel-Air mansion" />
        )}
        </Item>

        <Item label="Description of Listing" extra="Max length of 400 characters">
        {
          getFieldDecorator("description", {
            rules: [
              {
                required: true,
                message: "Please add a description."
              }
            ]
          })(
          <Input.TextArea rows={3} maxLength={400} placeholder="Clean, modern, and iconic home of the Fresh Prince. Situated in the heart of Bel-Air Los Angeles." />
        )}
        </Item>

        <Item label="Address">
        {
          getFieldDecorator("address", {
            rules: [
              {
                required: true,
                message: "Please add the address."
              }
            ]
          })(
          <Input placeholder="251 Bristol Ave." />
        )}
        </Item>

        <Item label="City/Town">
        {
          getFieldDecorator("city", {
            rules: [
              {
                required: true,
                message: "Please select city."
              }
            ]
          })(
          <Input placeholder="Los Angeles" />
        )}
        </Item>

        <Item label="State/Province">
        {
          getFieldDecorator("state", {
            rules: [
              {
                required: true,
                message: "Please select your State."
              }
            ]
          })(
          <Input placeholder="California" />
        )}
        </Item>

        <Item label="Zip code">
        {
          getFieldDecorator("postalCode", {
            rules: [
              {
                required: true,
                message: "Please select a Zip (or postal) code for your listing."
              }
            ]
          })(<Input placeholder="Please enter a zip code for your listing!" />)}
        </Item>

        <Item label="Image" extra="Images must be less than 1MB and in JPG or PNG format">
          <div className="host__form-image-upload">
          {
          getFieldDecorator("image", {
            rules: [
              {
                required: true,
                message: "Please provide an image for your listing."
              }
            ]
          })(
            <Upload 
              name="image"
              listType="picture-card"
              showUploadList={false}
              action="https://www.mocky.io/v2/5cc8019d300000980a055e76"
              beforeUpload={beforeImageUpload}
              onChange={handleImageUpload}
            >
              {imageBase64Value ? (
                <img src={imageBase64Value} alt="Listing" />
              ) : (
                <div>
                  <Icon type={imageLoading ? 'loading' : 'plus'} />
                  <div className="ant-upload-text">Upload</div>
                </div>
              )}
            </Upload>
          )}
          </div>
        </Item>
        
        <Item label="Price" extra="All prices in USD/day">
        {
          getFieldDecorator("price", {
            rules: [
              {
                required: true,
                message: "Please select a daily price for your listing."
              }
            ]
          })(
          <InputNumber min={0} placeholder="120" />
          )}
        </Item>

        <Item>
          <Button type="primary" htmlType="submit">Submit</Button>
        </Item>

      </Form>
    </Content>
  );
}

export const WrappedHost = Form.create<Props & FormComponentProps>({
 name: 'host_form' 
})(Host);