import React from 'react';
import { useQuery } from '@apollo/react-hooks';
import { Layout, Typography, Col, Card, Row } from 'antd';
import { displayErrorMessage } from '../../lib/utils/index';
import { Link, RouteComponentProps } from 'react-router-dom';
import { 
  Listings as ListingsData, ListingsVariables 
} from '../../lib/graphql/queries/Listings/__generated__/Listings';
import { LISTINGS } from '../../lib/graphql/queries';
import { ListingsFilter } from '../../lib/graphql/globalTypes';
import { HomeHero, HomeListings, HomeListingsSkeleton } from './components/';
import sanFranciscoImage from './assets/san-fransisco.jpg';
import cancunImage from './assets/cancun.jpg';
import mapBackground from './assets/map-background.jpg';

const { Content } = Layout;
const { Title, Paragraph } = Typography;

const PAGE_LIMIT = 4;
const PAGE_NUMBER = 1;

export const Home = ({ history }: RouteComponentProps) => {
  const { loading, data } = useQuery<ListingsData, ListingsVariables>(LISTINGS, {
    variables: {
      filter: ListingsFilter.PRICE_HIGH_TO_LOW,
      limit: PAGE_LIMIT,
      page: PAGE_NUMBER,
    }
  });

  const onSearch = (value: string) => {
    const trimmedValue = value.trim();
    if (trimmedValue) {
      history.push(`/listings/${trimmedValue}`);
    } else {
      displayErrorMessage("Please enter a valid search");
    }
  }

  const renderListingsSection = () => {
    if (loading) {
      return <HomeListingsSkeleton />;
    }

    if (data) {
      return <HomeListings title="Premium Listings" listings={data.listings.result} />
    }

    return null;
  }
  return (
    <Content className="home" style={{ backgroundImage: `url(${mapBackground})`}}>
      <HomeHero onSearch={onSearch} />
      <div className="home__cta-section">
        <Title level={2} className="home__cta-section-title">
          Your Guide To All Things Rental
        </Title>
        <Paragraph>
          Helping You Make The Best Rentals In Your Last Minute Destinations. 
        </Paragraph>
        <Link 
          to="/listings/united%20states"
          className="ant-btn ant-btn-primary ant-btn-lg home__cta-section-button"
        >
          Popular Listings In The United States
        </Link>
      </div>
      <div>
        {renderListingsSection()}
      </div>
      <div className="home__listings">
        <Title level={4}>Listings of Any Kind</Title>
        <Row gutter={12}>
          <Col xs={24} md={12}>
            <Link to="/listings/san%20francisco/">
              <div className="home__listings-img-cover">
                <img src={sanFranciscoImage} alt="San Francisco" className="home__listings-img"/>
              </div>
            </Link>
          </Col>
          <Col xs={24} md={12}>
            <Link to="/listings/cancun/">
              <div className="home__listings-img-cover">
                <img src={cancunImage} alt="Cancun" className="home__listings-img"/>
              </div>
            </Link>
          </Col>
        </Row>
      </div>
    </Content>
  )
}