import React from 'react';
import { Alert } from 'antd';

interface Props { 
  message?: string;
  description?: string;
}

export const ErrorBanner = ({
  message="uh oh something failed :(", description="Something broke, please check your network connection." 
}: Props) => {
  return (
    <Alert 
      banner
      closable
      message={message}
      description={description}
      type="error"
      className="error-banner"
    />
  )
}