import React, { Fragment } from 'react';
import { User as UserData } from '../../../../lib/graphql/queries/User/__generated__/User';
import { Card, Typography, Divider, Tag, Avatar, Button } from 'antd';
import { displayPrice, displaySuccessNotification, displayErrorMessage } from '../../../../lib/utils';
import { disconnectStripe as DisconnectStripeData } from '../../../../lib/graphql/mutations/DisconnectStripe/__generated__/disconnectStripe';
import { useMutation } from '@apollo/react-hooks';
import { Viewer } from '../../../../lib/types';
import { DISCONNECT_STRIPE } from '../../../../lib/graphql/mutations';

interface Props {
  user: UserData["user"];
  viewer: Viewer;
  viewerIsUser: boolean;
  setViewer: (viewer: Viewer) => void;
  handleRefetch: () => void;
}

const stripeAuthUrl = `https://connect.stripe.com/oauth/authorize?response_type=code&client_id=${process.env.REACT_APP_S_CLIENT_KEY}&scope=read_write`
const redirectToStripe = () => {
  window.location.href = stripeAuthUrl;
}

const { Title, Paragraph, Text } = Typography;

export const UserProfile = ({ user, viewer, viewerIsUser, setViewer, handleRefetch }: Props) => {
  const [disconnectStripe, { loading }] = useMutation<DisconnectStripeData>(DISCONNECT_STRIPE, {
    onCompleted: data => {
      if (data && data.disconnectStripe) {
        setViewer({ ...viewer, hasWallet: data.disconnectStripe.hasWallet });
        displaySuccessNotification("You've successfully disconnected from Stripe.", 
        "You'll have to reconnect with Stripe to continue to create listings.");
        handleRefetch();
      }
    },
    onError: () => {
      displayErrorMessage("Sorry we were not able to disconnect from Stripe. Please try again!")
    }
  });
  const additionalStripeDetails = user.hasWallet ? 
  <Fragment>
    <Paragraph>
      <Tag color="green">Stripe Registered</Tag>
    </Paragraph>
    <Paragraph>
      Income Earned: {" "}
      <Text strong>{user.income ? displayPrice(user.income) : `$0`}</Text>
    </Paragraph>
    <Button 
      type="primary" 
      className="user-profile__details-cta"
      loading={loading}
      onClick={() => disconnectStripe()}
    >Disconnect Stripe</Button>
    <Paragraph type="secondary">By disconnecting Stripe, you won't be able to receive{" "}
    <Text strong>any further payments</Text>. This will prevent users from booking listings you may have already created.
    </Paragraph>
  </Fragment> : (
    <Fragment>
      <Paragraph>
          Interested in becoming a TinyHouse host? Set up an account with Stripe!
        </Paragraph>
        <Button 
          type="primary" 
          className="user-profile__details-cta"
          onClick={redirectToStripe}
        >Connect With Stripe</Button>
        <Paragraph type="secondary">We use <a href="https://stripe.com/en/connect/" target="_blank" rel="noopener noreferrer">Stripe</a> to pay out hosts in an easy and secure way.</Paragraph>
    </Fragment>
  )

  const additionalDetails = viewerIsUser ? (
    <Fragment>
      <Divider />
      <div className="user-profile__details">
        <Title level={4}>Additional Details</Title>
        {additionalStripeDetails}
      </div>
    </Fragment>
  ) : null;

  return (
    <div className="user-profile">
      <Card className="user-profile__card">
        <div className="user-profile__avatar">
          <Avatar size={100} src={user.avatar} /> 
        </div>
        <Divider />
        <div className="user-profile__details">
          <Title level={4}>Details</Title>
          <Paragraph>
            Name: <Text strong>{user.name}</Text>
          </Paragraph>
          <Paragraph>
            Contact: <Text strong>{user.contact}</Text>
          </Paragraph>
        </div>
        {additionalDetails}
      </Card>
    </div>
  )
}

